<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="decrementAmount()">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="currentAmount">

    <button type="button" aria-label="Добавить один товар" @click.prevent="incrementAmount()">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['amount'],
  computed: {
    currentAmount: {
      get() {
        return this.amount;
      },
      set(value) {
        this.$emit('changeAmount', value);
      },
    },
  },
  model: {
    prop: 'amount',
    event: 'changeAmount',
  },
  methods: {
    incrementAmount() {
      this.currentAmount += 1;
    },
    decrementAmount() {
      if (this.currentAmount > 1) this.currentAmount -= 1;
    },
  },
};
</script>
