<template>
  <div>
    <ul class="cart__orders">
      <li class="cart__order" v-for="item in products" :key="item.productId" :item="item">
        <h3>{{ item.product.title }}</h3>
        <b>{{ item.product.price * item.amount | numberFormat }} ₽</b>
        <span>Артикул: {{ item.productId }}</span>
      </li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <b>{{ totalAmount + ' ' }} </b>
      <ItemLabel :totalAmount="totalAmount" :totalAmountEnd="totalAmountEnd"/>
      на сумму <b>{{ totalPrice | numberFormat }}&nbsp;₽</b></p>
    </div>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import ItemLabel from '@/components/ItemLabel.vue';
import returnLastChar from '@/helpers/returnLastChar';

export default {
  components: { ItemLabel },
  filters: { numberFormat },
  props: ['products', 'totalPrice', 'totalAmount'],
  computed: {
    totalAmountEnd() {
      return returnLastChar(this.totalAmount);
    },
  },
};
</script>
